/*Variable Declarations*/
/*Main Styles*/
* {
  box-sizing: border-box;
}
a {
  color: inherit;
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  font-family: 'Droid Serif', serif;
}
.main {
  background: url("/../image/wave.jpg") no-repeat center center fixed;
  background-size: cover;
}
h1,
h2,
h3,
h4 {
  margin: 0;
}
.base__title {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
.base__pageTitle {
  background-color: rgba(230,230,230,0.7);
  border: 4px solid #fff;
}
.base__button {
  border: 0;
  cursor: pointer;
}
.base__button:hover {
  text-decoration: underline;
}
.base__bar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  width: 100%;
  border-bottom: 1px solid #808080;
}
.base__bar a {
  transition: all ease-in-out 0.3s;
}
.base__bar a i {
  vertical-align: middle;
  padding-bottom: 3px;
}
.base__bar a:hover {
  text-decoration: underline;
  color: #808080;
}
.base__header {
  background-color: rgba(230,230,230,0.7);
  border: 4px solid #fff;
  display: flex;
  width: 100%;
  padding-left: 0.5em;
  padding-right: 0.5em;
  justify-content: space-between;
  align-items: center;
}
/*Layouts*/
.main__header {
  background-color: rgba(230,230,230,0.7);
  display: flex;
  align-items: center;
  padding-left: 10%;
  padding-right: 10%;
  border-bottom: 1px solid #808080;
}
.main__logo {
  height: 100px;
  width: 100px;
}
.main__title {
  margin-left: auto;
}
.main__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
}
.main__nav--item {
  font-weight: bold;
  display: block;
  padding: 20px 30px;
  transition: all ease-in-out 0.3s;
}
.main__nav--item a {
  display: block;
  color: inherit;
}
.main__nav--item:hover {
  color: #e3a95a;
}
.main__content {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  padding: 2em;
  flex: 1;
}
.adminPanel__header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 0.5em;
  padding-right: 0.5em;
  align-items: center;
  color: #fff;
  background-color: #222;
}
.adminPanel__header--buttons {
  display: flex;
  color: #ababab;
}
.adminPanel__header--button {
  padding: 1em;
  transition: all ease-in-out 0.3s;
}
.adminPanel__header--button i {
  font-size: 50px;
}
.adminPanel__header--button:hover {
  color: #fff;
}
.adminPanel__container {
  display: flex;
  flex-direction: row;
  flex: 1;
}
.adminPanel__nav {
  display: flex;
  flex-direction: column;
  flex: 1;
  background-color: #444;
  color: #ababab;
}
.adminPanel__nav--item {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid #363636;
  padding: 1em;
  text-align: center;
  transition: all ease-in-out 0.3s;
}
.adminPanel__nav--item:hover {
  color: #fff;
  background-color: #333232;
}
.adminPanel__nav--item span {
  margin-left: 1em;
}
.adminPanel__content {
  flex: 5;
  box-shadow: -2px 0px 5px 0px #000;
}
/*Components*/
.featured__container {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-around;
  align-items: flex-start;
}
.featured__col {
  background-color: rgba(230,230,230,0.7);
  border: 4px solid #fff;
  display: flex;
  flex-direction: column;
  border: 4px solid #fff;
  height: 70%;
  padding: 0.5em;
  margin: 0.5em;
  flex: 1;
}
.featured__col .base__title {
  text-align: center;
}
.featured__col .material-icons {
  align-self: center;
  font-size: 150px;
}
.login__container {
  display: flex;
  justify-content: center;
}
.login__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.detailedList__container {
  display: flex;
  flex-direction: column;
  flex: 1;
}
.detailedList__details {
  display: flex;
  box-shadow: 0px 1px 1px 0px #808080;
  padding-top: 0.5em;
  padding-bottom: 0.5em;
}
.detailedList__details span {
  text-decoration: underline;
  font-weight: bold;
}
.detailedList__details .child {
  min-width: 20%;
}
.detailedList__details .child:first-child {
  padding-left: 1em;
}
.detailedList__item {
  display: flex;
  width: 100%;
  padding: 1em;
  border-bottom: 1px solid #ababab;
  color: #ababab;
  transition: all ease-in-out 0.3s;
}
.detailedList__item .child {
  min-width: 20%;
}
.detailedList__item:hover {
  background-color: #f0f0f0;
}
.detailedList__item--buttons {
  display: flex;
  margin-left: auto;
}
.detailedList__item--buttons .material-icons {
  margin-left: 0.5em;
}
.detailedList__item--buttons .material-icons:hover {
  color: #000;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1em;
}
.form label {
  margin-bottom: 0.5em;
  margin-top: 0.5em;
}
.form input,
.form textarea {
  padding: 0.5em;
}
.productIndex__container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: 1em;
}
.productIndex__item {
  background-color: rgba(230,230,230,0.7);
  border: 4px solid #fff;
  box-shadow: 0px 17px 10px -10px rgba(0,0,0,0.2);
  transition: all ease-in-out 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 0.5em;
  margin-top: 1em;
}
.productIndex__item:hover {
  box-shadow: 0px 37px 20px -10px rgba(0,0,0,0.2);
  transform: translate(0px, -10px);
}
.productIndex__item img {
  height: 250px;
  width: 250px;
  border-radius: 50%;
  border: 4px solid #000;
  backface-visibility: hidden;
}
.productIndex__item:hover h3 {
  text-decoration: underline;
}
.productShow__container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1em;
}
.productShow__col {
  background-color: rgba(230,230,230,0.7);
  border: 4px solid #fff;
  display: flex;
  flex-direction: column;
  padding: 1em;
  min-width: 30%;
}
.productShow__col img {
  height: 300px;
  width: 300px;
  border-radius: 25%;
  border: 4px solid #000;
}
